.checkbox {
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 3;
  margin: 0;
  padding: 0;
  position: relative;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  z-index: 1;
  background-color: #ebf7fc;
  transition: all .3s;
}

#theme-button .knobs:before, #theme-button .knobs:after, #theme-button .knobs span {
  width: 20px;
  height: 10px;
  text-align: center;
  border-radius: 30px;
  padding: 9px 4px;
  font-size: 10px;
  font-weight: bold;
  line-height: 1;
  transition: all .3s;
  position: absolute;
  top: 4px;
}

#theme-button .knobs:before, #theme-button .knobs:after {
  color: #4e4e4e;
  z-index: 1;
}

#theme-button .knobs:before {
  content: "Dark";
  left: 4px;
}

#theme-button .knobs:after {
  content: "Light";
  right: 4px;
}

#theme-button .knobs span {
  width: 25px;
  z-index: 2;
  color: #fff;
  background-color: #67b799;
  left: 37px;
}

#theme-button .checkbox:checked + .knobs span {
  background-color: #67b799;
  left: 4px;
}

#theme-button .checkbox:checked ~ .layer {
  background-color: #fcebeb;
}

.toggle-button-cover {
  display: flex;
}

